.mapped-inputs{
    padding: 0 10px;
}

.add-btn{
 border: none;
 border-radius: 5px;
 width: 65px;
 right: 76px;
 margin: 5px 0px 15px;
 font-size: 2.5rem;
 font-weight: bold;
 background-color: hsla(235, 57%, 60%, 1);
 color: white;
transition: 0.3s;}

.add-btn:active{
 font-size: 2rem;
color: hsla(235, 57%, 60%, 1);
 background-color: white;}

label{display: flex;
    gap: 70px;
    margin-right: 60px;
    justify-content: flex-start;}

.inputs{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 11px;
    height: 65px;
}

.inputs > .input-text{
    width: 70%;
}

.inputs > .price{
    width: 40%;
}

input{
    height: 30%;
    padding:8px 5px;
    outline: none;
border-radius: 5px;
border: 1px solid black;
font-size: 16px;
}

input:focus{
border-color:hsla(235, 57%, 60%, 1);
color: hsla(235, 57%, 60%, 1);

}

p{
    
 font-size:1rem ;
}

#delete{ 
    font-weight: bold;
    color: red;
    font-size: 2.3rem;
    transition: 0.3s;
}

#delete:active{ 
    font-size: 2rem;
}
